/* Head Yellow */
#aj-head-yellow {
  opacity: 0;
  transform: opacity;
  transform-origin: center;
  animation: appear 1.5s ease-in-out forwards;
}
#glassesR-yellow {
  transform: translateY(35px) scaleY(.50);
  animation: glassesAnimR 3s .6s forwards;
  -webkit-animation-fill-mode: forwards;/* Safari 4.0 - 8.0 */
}
#glassesL-yellow {
  transform: translateY(35px) scaleY(.50);
  animation: glassesAnim 1.5s .6s forwards;
  -webkit-animation-fill-mode: forwards;/* Safari 4.0 - 8.0 */
}
#earringsR-yellow {
  transform: translateY(-5px);
  animation: earringsAnimR 3s .6s forwards;
  -webkit-animation-fill-mode: forwards;/* Safari 4.0 - 8.0 */
}
#earringsL-yellow {
  transform: translateY(-5px);
  animation: earringsAnim 1.5s .6s forwards;
  -webkit-animation-fill-mode: forwards;/* Safari 4.0 - 8.0 */
}
#face-yellow {
  transform: translateY(50px) scaleY(.64);
  animation: faceAnim 3s .6s forwards;
  -webkit-animation-fill-mode: forwards;/* Safari 4.0 - 8.0 */
}
#bangsR-yellow {
  transform: translateY(55px) scaleY(.64);
  animation: bangsAnim 3s .6s forwards;
  -webkit-animation-fill-mode: forwards;/* Safari 4.0 - 8.0 */
}
#bangsL-yellow {
  transform: translateY(50px) scale(.80);
  animation: bangsAnim 3s .6s forwards;
  -webkit-animation-fill-mode: forwards;/* Safari 4.0 - 8.0 */
}
#hairBack-yellow {
  transform: translate(0px, 20px) scaleY(.80) ;
  animation: hairBackAnim 3s .6s forwards;
  -webkit-animation-fill-mode: forwards;/* Safari 4.0 - 8.0 */
}


