/* Headbang Animation */
@keyframes glassesAnim {
  20% {
    transform: translateY(-10px) scaleY(1);
  }
  40% {
    transform: translateY(-10px) scaleY(1);
  }
  60% {
    transform: translateY(25px) scaleY(.7);
  }
  80% {
    transform: translateY(-10px) scaleY(1);
  }
  100% {
    transform: translateY(0px) scaleY(1);
  }
}

@keyframes glassesAnimR {
  10% {
    transform: translateY(-10px) scaleY(1);
  }
  20% {
    transform: translateY(-10px) scaleY(1);
  }
  30% {
    transform: translateY(25px) scaleY(.7);
  }
  40% {
    transform: translateY(-10px) scaleY(1);
  }
  50% {
    transform: translateY(0px) scaleY(1);
  }
  60% {
    transform: translateY(20px) scaleY(.1);
  }
  70% {
    transform:  scaleY(1);
  }
  100% {
    transform:  scaleY(1);
  }
}

@keyframes faceAnim {
  10% {
    transform: translateY(-5px) scaleY(1.1);
  }
  20% {
    transform: translateY(-5px) scaleY(1.1);
  }
  30% {
    transform: translateY(30px) scaleY(.8);
  }
  40% {
    transform: translateY(-5px) scaleY(1);
  }
  50% {
    transform: translateY(0px) scaleY(1);
  }
  60% {
    transform: rotateZ(2deg);
  }
  70% {
    transform: rotateZ(0deg);
  }
  100% {
    transform: rotateZ(0deg);
  }
}

@keyframes bangsAnim {
  10% {
    transform: translateY(5px) scaleY(1);
  }
  20% {
    transform: translateY(5px) scaleY(1);
  }
  30% {
    transform: translateY(38px) scaleY(.7);
  }
  40% {
    transform: translateY(-5px) scaleY(1);
  }
  50% {
    transform: translateY(0px) scaleY(1);
  }
  60% {
      transform: rotateZ(2deg);
  }
  70% {
    transform: rotateZ(0deg);
  }
  100% {
    transform: rotateZ(0deg);
  }
}
@keyframes hairBackAnim {
  10% {
    transform: translateY(5px) scaleY(1);
  }
  20% {
    transform: translateY(5px) scaleY(1);
  }
  30% {
    transform: translateY(-2px) scaleY(.85);
  }
  40% {
    transform: translateY(10px) scaleY(1);
  }
  50% {
    transform: translateY(5px) scaleY(1);
  }
  60% {
      transform: rotateZ(1deg);
  }
  70% {
    transform: rotateZ(0deg);
  }
  100% {
    transform: rotateZ(0deg);
  }
}
@keyframes earringsAnim {
  20% {
    transform: translateY(-10px) scaleY(1);
  }
  40% {
    transform: translateY(-10px) scaleY(1);
  }
  60% {
    transform: translateY(5px) scaleY(1);
  }
  80% {
    transform: translateY(-5px) scaleY(1);
  }
  100% {
    transform: translateY(0px) scaleY(1);
  }
}
@keyframes earringsAnimR {
  10% {
    transform: translateY(-10px) scaleY(1);
  }
  20% {
    transform: translateY(-10px) scaleY(1);
  }
  30% {
    transform: translateY(5px) scaleY(1);
  }
  40% {
    transform: translateY(-5px) scaleY(1);
  }
  50% {
    transform: translateY(0px) scaleY(1);
  }
  60% {
    transform: translate(-2px, 3px);
  }
  70% {
    transform: translateY(0px);
  }
  100% {
    transform: translateY(0px);
  }
}

/* Wink Animation */
@keyframes winkAnim {
  20% {
    transform: rotateZ(2deg);
  }
  30% {
      transform: rotateZ(0deg);
  }
  100% {
      transform: rotateZ(0deg);
  }
}

/* Circle Beat Animation */
@keyframes circleBeatAnim {
  0% {
    opacity: 1;
  }
  100% {
    transform: scale(1000);
    opacity: 0;
  }
}



@keyframes appear {
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
